<template>
  <el-row class="container">
    <!--头部-->
    <el-col :span="24" class="topbar-wrap">
      <div class="topbar-logo topbar-btn">
        <a href="/"><img src="../assets/images/logo.png" style="padding-left:8px;"></a>
      </div>
      <div class="topbar-logos" v-show="!collapsed">
        <a href="/"><img src="../assets/images/logo2.png"></a>
      </div>
      <div class="topbar-title">
        <span style="font-size: 18px;color: #fff;">备份管理系统</span>
      </div>
      <div style="float: right;margin-right: 15px;color: #fff">
        <el-tooltip content="退出登录" placement="bottom">
          <svg class="icon" aria-hidden="true" @click="logout">
            <use xlink:href="#icon-logout"></use>
          </svg>
        </el-tooltip>
      </div>
      <div class="topbar-account topbar-btn">
        <el-dropdown trigger="click">
          <span class="el-dropdown-link userinfo-inner">
            <svg class="icon-static" aria-hidden="true">
              <use xlink:href="#icon-user"></use>
            </svg> {{sysUserName}}
            <svg class="icon-static" aria-hidden="true">
              <use xlink:href="#icon-down"></use>
            </svg>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-if="role != 'superrole'">
              <router-link to="/selfinfo/profile"><span style="color: #555;font-size: 14px;">个人信息</span></router-link>
            </el-dropdown-item>
            <el-dropdown-item>
              <router-link :to="'/selfinfo/changepwd'"><span style="color: #555;font-size: 14px;">修改密码</span>
              </router-link>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-col>

    <!--中间-->
    <el-col :span="24" class="main">
      <!--左侧导航-->
      <aside :class="{showSidebar:!collapsed}">
        <!--展开折叠开关-->
        <div class="menu-toggle" @click.prevent="collapse">
          <svg class="icon" aria-hidden="true" v-show="!collapsed">
            <use xlink:href="#icon-fold"></use>
          </svg>
          <svg class="icon" aria-hidden="true" v-show="collapsed">
            <use xlink:href="#icon-unfold"></use>
          </svg>
        </div>
        <!--导航菜单-->
        <el-menu default-active="0" router :collapse="collapsed">
          <template v-for="(item,index) in $router.options.routes"
                    v-if="item.menuShow && ((role == 'superrole' || role == 'admin')? item.menuShow : !item.isSuperAdm)">
            <el-submenu v-if="!item.leaf" :index="index+''">
              <template slot="title">
                <svg class="icon" aria-hidden="true">
                  <use :xlink:href="item.iconCls"></use>
                </svg>
                <span slot="title">{{item.name}}</span></template>
              <el-menu-item v-for="term in item.children" :key="term.path" :index="term.path" v-if="term.menuShow"
                            :class="$route.path==term.path?'is-active':''">
                <svg class="icon" aria-hidden="true">
                  <use :xlink:href="term.iconCls"></use>
                </svg>
                <span slot="title">{{term.name}}</span>
              </el-menu-item>
            </el-submenu>
            <el-menu-item v-else-if="item.leaf&&item.children&&item.children.length" :index="item.children[0].path"
                          :class="$route.path==item.children[0].path?'is-active':''">
              <svg class="icon" aria-hidden="true">
                <use :xlink:href="item.iconCls"></use>
              </svg>
              <span slot="title">{{item.children[0].name}}</span>
            </el-menu-item>
          </template>
        </el-menu>
      </aside>

      <!--右侧内容区-->
      <section class="content-container">
        <div class="grid-content bg-purple-light">
          <el-col :span="24" class="content-wrapper">
            <transition name="fade" mode="out-in">
              <router-view></router-view>
              <!--<router-view :roles="roles" :groups="groups" @transferRoles="refreshRoles"-->
              <!--@transferGroups="refreshGroups"></router-view>-->
            </transition>
          </el-col>
        </div>
      </section>
    </el-col>

  </el-row>
</template>

<script>
  import {reqGetUserProfile, reqGetRoleList, reqGetGroupList} from '../api/api';
  import {bus} from '../bus.js'
  export default {
    name: 'home',
    created(){
      //bus.$on('setUserName', (text) => {
      //  this.sysUserName = text;
      //})
    },
    data () {
      return {
        sysUserName: '',
        sysUserAvatar: '',
        collapsed: false,
        roles:[],
        groups:[],
        role: ''
      }
    },
    methods: {
      refreshRoles(roles){
        this.roles = roles;
      },
      refreshGroups(groups){
        this.groups = groups;
      },
      getRoles(username){
        let params = {
          user: username
        };
        reqGetRoleList(params).then(res => {
          let { status, data } = res;
          if (data == null) {
            console.log("无法获取角色列表");
          } else {
            this.roles = data.roles;
            //bus.$emit('roles', data.roles);
          }
        },err => {
          if (err.response.status == 401) {
            this.$message({
              message: "请重新登陆",
              type: 'error'
            });
            sessionStorage.removeItem('access-user');
            this.$router.push({ path: '/' });
          } else {
            this.$message({
              message: "请求异常",
              type: 'error'
            });
          }
        });
      },
      getGroups(username){
        let params = {
          user: username
        };
        reqGetGroupList(params).then(res => {
          let { status, data } = res;
          if (data == null) {
            console.log("无法获取组列表");
          } else {
            this.groups = data.groups;
            //bus.$emit('groups', data.groups);
          }
        },err => {
          if (err.response.status == 401) {
            this.$message({
              message: "请重新登陆",
              type: 'error'
            });
            sessionStorage.removeItem('access-user');
            this.$router.push({ path: '/' });
          } else {
            this.$message({
              message: "请求异常",
              type: 'error'
            });
          }
        });
      },
      handleOpen() {
        //console.log('handleopen');
      },
      handleClose() {
        //console.log('handleclose');
      },
      //折叠导航栏
      collapse: function () {
        this.collapsed = !this.collapsed;
      },
      showMenu(i, status){
        this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-' + i)[0].style.display = status ? 'block' : 'none';
      },
      logout(){
        var _this = this;
        this.$confirm('确认退出吗?', '提示', {
          //type: 'warning'
        }).then(() => {
          sessionStorage.removeItem('access-user');
          _this.$router.push('/login');
        }).catch(() => {

        });
      }
    },
    mounted() {
      var accessInfo = sessionStorage.getItem('access-user');
      if (accessInfo) {
        accessInfo = JSON.parse(accessInfo);
        this.sysUserName = accessInfo.username;
        this.role = accessInfo.role;
        //this.getRoles(this.sysUserName);
        //this.getGroups(this.sysUserName);
      } else {
        this.$router.push('/login');
      }
    }
  }


</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .container {
    position: absolute;
    top: 0px;
    bottom: 0px;
    width: 100%;

    .topbar-wrap {
      height: 50px;
      line-height: 50px;
      background: #373d41;
      padding: 0px;

      .topbar-btn {
        color: #fff;
      }
      /*.topbar-btn:hover {*/
      /*background-color: #4A5064;*/
      /*}*/
      .topbar-logo {
        float: left;
        width: 59px;
        line-height: 26px;
      }
      .topbar-logos {
        float: left;
        width: 120px;
        line-height: 26px;
      }
      .topbar-logo img, .topbar-logos img {
        height: 40px;
        margin-top: 5px;
        margin-left: 2px;
      }
      .topbar-title {
        float: left;
        text-align: left;
        width: 200px;
        padding-left: 10px;
        border-left: 1px solid #000;
      }
      .topbar-account {
        float: right;
        padding-right: 5px;
      }
      .userinfo-inner {
        cursor: pointer;
        color: #fff;
        padding-left: 10px;
      }
    }
    .main {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      position: absolute;
      top: 50px;
      bottom: 0px;
      overflow: hidden;
    }

    aside {
      min-width: 50px;
      background: #333744;
      &::-webkit-scrollbar {
        display: none;
      }

      &.showSidebar {
        overflow-x: hidden;
        overflow-y: auto;
      }

      .el-menu {
        height: 100%; /*写给不支持calc()的浏览器*/
        height: -moz-calc(100% - 80px);
        height: -webkit-calc(100% - 80px);
        height: calc(100% - 80px);
        border-radius: 0px;
        background-color: #333744;
      }

      .el-submenu .el-menu-item {
        min-width: 60px;
      }
      .el-menu {
        width: 180px;
      }
      .el-menu--collapse {
        width: 60px;
      }

      .el-menu .el-menu-item, .el-submenu .el-submenu__title {
        height: 46px;
        line-height: 46px;
      }

      .el-menu-item:hover, .el-submenu .el-menu-item:hover, .el-submenu__title:hover {
        background-color: #7ed2df;
      }
    }

    .menu-toggle {
      background: #4A5064;
      text-align: center;
      color: white;
      height: 26px;
      line-height: 30px;
    }

    .content-container {
      background: #fff;
      flex: 1;
      overflow-y: auto;
      padding: 10px;
      padding-bottom: 1px;

      .content-wrapper {
        background-color: #fff;
        box-sizing: border-box;

      }
    }
  }
</style>



